
import avatarSrc from '../userHeader/avator.png'
import Scroll from '../../../base/scroll/scroll'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { mapActions, mapGetters } from 'vuex'
import { userInfoMixin } from '../../../assets/js/mixin'
export default {
name: "settings",
mixins: [userInfoMixin],
filters: {
isNull: function (value) {
if (value) {
return value
}
return '未填写'
}
},
components: {
Group,
Cell,
Scroll,
Actionsheet,
Datetime,
PopupPicker
},
computed: {
avatorBox: {
get() {
return {
title: '头像',
link: '',
isLink: true,
avator: this.userInfo.headPicId || avatarSrc
}
},
set(val) {
return val
}
},
userName: {
get() {
return {
title: '用户名',
link: '',
isLink: false,
value: this.userInfo.username || this.userInfo.telephone || '暂无数据'
}
},
set(val) {
return val
}
},
userTaobao: {
get() {
return {
title: '旺旺等级',
link: '',
isLink: true,
value: this.list[0][parseInt(this.userInfo.taobaoLevel)] || '暂无数据',
info: '请及时更新真是的旺旺等级'
}
},
set(val) {
return val
}
},
userSex: {
get() {
return {
title: '性别',
link: 'settings/usersex',
isLink: true,
value: (parseInt(this.userInfo.gender) === 0 ? '女' : '男')
}
},
set(val) {
return val
}
},
userAge: {
get() {
return {
title: '出生日期',
link: '',
isLink: true,
value: this.userInfo.birthday ? this.userInfo.birthday.slice(0, 10) : this.userInfo.birthday
}
},
set(val) {
return val
}
},
userAddress: {
get() {
return {
title: '收货地址',
link: 'settings/userAddress',
isLink: true,
value: this.userInfo.postAddress
}
},
set(val) {
return val
}
},
boxTwo: {
get() {
return [
{
title: '联系QQ',
link: 'settings/userQq',
isLink: true,
value: this.userInfo.qqNum,
info: '填写有助于提高中奖率'
},
{
title: '更改手机号',
link: 'settings/userPhone',
isLink: true,
value: this.userInfo.telephone
}
]
},
set(val) {
return val
}
},
...mapGetters([
'userInfo'
])
},
data() {
return {
show: false,
menus2: {
menu1: '从相册选择',
menu2: '拍照'
},
visibility: false,
age: '',
list: [['1星', '2星', '3星', '4星', '5星', '1冠', '2冠', '3冠', '4冠', '5冠']],
value: [],
taobaoLevel: null,
userTaobaoState: null,
showPopupPicker: false
}
},
methods: {

isNull(value) {
if (value) {
return value
}
return '未填写'
},

avatorChange() {
this.show = true
},

ageChange() {
this.visibility = true
},

saveAge() {
this.$axios.post('/api/user/update', {
telephone: this.userInfo.telephone,
birthday: this.age
}).then((response) => {
if (response.data.code === '200') {
let _this = this
let obj = Object.assign({}, this.userInfo, {
birthday: this.age
})
this.userAge.value = this.age
this.setUserInfo(obj)
this.$vux.toast.show({ text: '设置成功' })
} else {
this.$vux.alert.show({
title: '错误提示',
content: response.data.message,
})
}

}).catch((error) => {
this.$vux.alert.show({
title: '错误提示',
content: '网络错误',
})
})
},

logout() {
const _this = this
this.$vux.confirm.show({
title: '确认退出登录吗',
content: '退出登录将不能查看个人信息',
onConfirm() {
_this.$axios.post('/api/user/loginOut', {}).then((response) => {
if (response.data.code === '200') {
_this.$vux.toast.text('退出成功', 'middle')
_this.setUserInfo('')
_this.$router.push({ name: 'login' })
} else {
_this.$vux.toast.show({
text: '退出成功',
type: 'warn',
position: middle
})
}
}).catch((error) => {
console.log(error)
})
}
})
},
onChange(val) {
this.$axios.post('/api/user/update', {
telephone: this.userInfo.telephone,
taobaoLevel: this.list[0].indexOf(val[0])
}).then((response) => {
if (response.data.code === '200') {
let _this = this
let obj = Object.assign({}, this.userInfo, {
taobaoLevel: this.list[0].indexOf(val[0])
})
this.userTaobao.value = {
title: '旺旺等级',
link: '',
isLink: true,
value: this.list[0][parseInt(this.list[0].indexOf(val[0]))] || '暂无数据',
info: '请及时更新真是的旺旺等级'
}
console.log(this.userTaobao.value)
this.setUserInfo(obj)
this.$vux.toast.show({ text: '设置成功' })
} else {
this.$vux.alert.show({
title: '错误提示',
content: response.data.message,
})
}

}).catch((error) => {
this.$vux.alert.show({
title: '错误提示',
content: '网络错误',
})
})
},
...mapActions([
'setUserInfo'
])
}
}



